/* reset css */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


html,
body,
#root {
	position: fixed;
	top: 0; left: 0;
	width: 100%;
	height: 100%;
	background: ivory;
	user-select: none;
}


.top-nav-wrapper,
.top-nav-wrapper * {
		pointer-events: none;
}

.top-nav-wrapper > div > .inner {
	color: #fff;
	display: flex;
	justify-content: space-between ;
	padding-top: 10px;
}

.nav {
	display: flex;
	justify-content: space-between;
	padding-right: 20px;
}

.side {
	display: flex;
	padding-left: 20px;
	width: 100px;
	justify-content: space-between;
}
.side-bar, 
.logo {
	pointer-events: all;
	cursor: pointer;
}

  rect {
    transition: all .3s ease-in-out;
  }
.mobile-side-bar    .middle {
      x: 0;
      opacity: 0;
}


 .mobile-side-bar    .top {
      transform: rotate(45deg);
      transform-origin: center top;
      x: 50px;
      y: 35px;
    }
  .mobile-side-bar     .bottom {
      transform: rotate(-45deg);
      transform-origin: center top;
      x: -20px;
      y: 25px;
    }

.mobile_header {
	padding-left: 20px;
}
.nav > div {
	cursor: pointer;

		pointer-events: all;
	padding-left: 40px;
}
.mobile_wrapper {
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,.9);
    transform-origin: top center;
}

.topWords,
.topWords *
{
		pointer-events: none;
}

.topWords  > div > .wrapper {
	height: 100vh;
	padding: 50px;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: flex-start;
	color: white;
	opacity: 0;
}



.topWords > div  .bottom-desc  {
	line-height: 32px;
	word-wrap: break-word;

}


/* .topWords > div  p.check-words * { */
.check {

	cursor: pointer;
	color: #fff;
	border: none;
	background-color: transparent;
	outline: none;
	padding: 0;

}




.industry-wrapper,
.industry-wrapper *
{
		/* pointer-events: none; */
}

.industry-wrapper > div > .item {
	width: 300px;
	text-align: center;
	cursor: pointer;
	pointer-events: all;
	color: #fff;
		visibility: hidden;
}

.industry-wrapper > div > .item:hover .icon-wrapper {
	/* transform: scale3d(1.2,1.2,1); */
	transform: scale3d(1.1,1.1,1);
}

.industry-icon {
	width: 58px; height: 58px; display: inline-block; padding-top: 10px;
}

.eng-title {

	font-family: 'Microsoft JhengHei UI';
	font-size: 16px;
	font-weight: 700;
}

.industry-title {
	font-family: 'Microsoft JhengHei UI';
	font-size: 32px;

	font-weight: 700;
	line-height: 131%;
}

@media(max-width: 768px) {
	.eng-title {
		font-size: 12px;
		padding-bottom: 5px;
	}

	.industry-title {
		font-size: 14px;
		padding-bottom: 5px;
	}
}


.loading {
	color: #fff;
}

.ready-wrapper {
	position: relative;
}

.ready-wrapper::after {
	content:'点击进入了解玻色量子应用';
	color: #fff;

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate3d(-50%, -50%, 0);
	font-size: 16px;
}


.ready-wrapper:hover::after{

	content:'开始旅程';

}

.ready-img {
	width: 414px;
	height: 414px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 414px 414px;
	background-image: url('./assets/start/ready.png');
	cursor: pointer;
}


.ready-img:hover {

	width: 452px;
	height: 452px;

	background-size: 452px 452px;
	background-image: url('./assets/start/go.png');
}

.ready-words {
	text-align: center;
	color: #fff;

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate3d(-50%, -50%, 0);
	font-size: 16px;
}

.top-desc {

	font-family: 'Microsoft JhengHei UI';
	padding-bottom: 40px;
	font-size: 12px;
}

.title {
	font-family: 'Microsoft JhengHei UI';

	font-size: 32px;
	line-height: 131%;
	padding-bottom: 40px;
}

.bottom-desc {
	font-family: 'Microsoft JhengHei UI';
	padding-bottom: 40px;
	font-size: 11px;
}

.check {

	font-family: 'Microsoft JhengHei UI';
	font-size: 11px;

}

@media only screen and (min-width: 1025px) {

.top-desc {
	font-size: 24px;
}
.title {
	font-size: 64px;
}

.bottom-desc {

	width: 540px;
	font-size: 20px;

}
.check {
	font-size: 20px;

}

.topWords  > div > .wrapper {
        padding:0 10vw;
        align-items: flex-start
				
    }
}

.check-icon {
	width: 40px;
	height: 40px;
	vertical-align: middle;
	padding-left: 10px;
	opacity: 0;
}